@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&family=Josefin+Sans:ital,wght@1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:ital,wght@1,300&display=swap');
.post{
font-family: 'Josefin Sans', sans-serif;
padding-top: 10px;
margin-top: 30px;	
width: 800px;
display: inline-block;
float: left;
margin-left: 100px;
}
.user{
	margin-top: 20px;
	float: right;
	width: fit-content;
	border: 1px solid blue;
	font-family: 'Indie Flower', cursive;
    font-family: 'Josefin Sans', sans-serif;
    margin-right: 10px;
    padding: 10px;
}
.list{
	width: fit-content;
	float: left;
    margin-top: 50px;
}
.list-small-screen{
	display: none;
}
.user-img{
	width: 50px;
	height: 50px;
}
.card-img{
	width: 100px;
	height: 100px;
}
.content{
	margin-top: 20px;
	padding-left: 10px;
	padding-right: 10px;
	padding-bottom: 10px;
	font-size: 1.5em;
	font-family: 'Open Sans Condensed', sans-serif;
	line-height: 1.7em;
}
@media screen and (max-width: 500px){
	.post{
font-family: 'Josefin Sans', sans-serif;
padding-top: 10px;
margin-top: 10px;	
width: fit-content;
display: inline-block;
margin-left: 0px;
}
.user{
	margin-top: 10px;
	width: fit-content;
	border: 1px solid blue;
	font-family: 'Indie Flower', cursive;
    font-family: 'Josefin Sans', sans-serif;
    padding: 10px;
    margin: 10px;
    vertical-align: middle;
}
.list{
	display: none;
}
.list-small-screen{
	display: flex;
	width: fit-content;
}
}