@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap');

.title{
	font-family: 'Permanent Marker', cursive;
}
.description{
	font-family: 'Quicksand', sans-serif;
}
.image-blog{
	width: 400px;
	height: 300px;
}
.padding{
	padding-top: 20px;
	line-height: 1.2em;
	font-size: 1.2em;
	margin-top: 5px;
	margin-bottom: 5px;
}
.navbar{
	padding: 5px;
	margin-left: 0px;
	margin-right: 10px;
	padding-left: 20px;
	width: 100%;
}
.pl-20{
	margin-left: 60px;
	padding-left: 150px;
}
.nav-search{
margin-left: 250px;
}
@media screen and (max-width: 500px){
	.image-blog{
	width: 200px;
	height: 200px;
}
.slides{
	left: 0px;
	float: left;
	padding-left: 0px;
	margin-left: 0px;
}
.pl-20{
	margin-left: 0px;
	padding-left: 0px;
}
.nav-search{
margin-left: 0px;
}
}